
.navbar{
    color: white;
    font-size: 14px;
    position: fixed;
    top:0;
    width: 100%;
    z-index: 1000;
    transition: all 0.7s ease-out;
    background: linear-gradient(to top, transparent 0%, rgba(0, 0, 0, 0.3) 50%);

    &.scrolled{
        background-color: var(--main-color);

 
    }

    .container{
        padding: 0px 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 70px;

        .left{
            display: flex;
            align-items: center;
            
            img{
                height: 25px;
                margin-right: 40px;
            }
            
            span{
                margin-right: 20px;
                cursor: pointer;
            }
        }

        .right{
            display    : flex;
            align-items: center;
            
            .icon{
                margin: 0px 15px;
                cursor: pointer;
            }

            img {
                width: 30px;
                height: 30px;
                border-radius: 5px;
                object-fit: cover;
                cursor: pointer;
            }

            .profile{

                .options{
                    display: none;
                    background-color: var(--main-color);
                    border-radius: 5px;
                    
                }
                span{
                    padding: 10px 12px;
                    cursor: pointer;
                }

                &:hover{
                    .options{
                        display: flex;
                        flex-direction: column;
                        position: absolute;
                    }
                }
            }
        }

    }
}