.register{
    width: 100vw;
    height: 100vh;
    background: linear-gradient(
        to bottom, 
        rgba(0,0,0,0) 0%,
        rgba(0,0,0,1) 100%
        ),
        url("https://media-assets-05.thedrum.com/cache/images/thedrum-prod/s3-news-tmp-10557-netflixa--default--1280.jpg");
        background-size: cover;
        position: relative;

    .top{

        .wrapper{
            padding: 20px 50px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .logo{
                height: 40px;
            }

            .loginButton{
                background-color: #d91921;
                border: none;
                color: white;
                border-radius: 5px;
                padding: 5px 15px;
                font-size: 16px;
                font-weight: 500;
                cursor: pointer;

            }
        }
    }

    .container{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: white;

        h1{
            font-size: 50px;
        }

        h2{
            margin: 20px;
        }

        p{
            font-size: 20px;
        }

        .input{
            width: 50%;
            background-color: white;
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 20px;
            height: 50px;
            border-radius: 5px;
            overflow: hidden;

            input{
                flex: 9;
                height: 100%;
                padding-left: 10px;
                border: none;
                font-size: 16px;
            }

            button{
                flex: 3;
                height: 100%;
                background-color: #d91921;
                border: none;
                color: white;
                font-size: 16px;
                // font-weight: 500;
                cursor: pointer;
            }
        }
    }

    
}