.login{
    width: 100vw;
    height: 100vh;
    background: linear-gradient(
        to bottom, 
        rgba(0,0,0,0) 0%,
        rgba(0,0,0,1) 100%
        ),
        url("https://media-assets-05.thedrum.com/cache/images/thedrum-prod/s3-news-tmp-10557-netflixa--default--1280.jpg");
        background-size: cover;
        position: relative;

    .top{

        .wrapper{
            padding: 20px 50px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .logo{
                height: 40px;
            }

            
        }
    }

    .container{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: white;

        form{
            width: 300px;
            height: 300px;
            padding: 30px;
            border-radius: 5px;
            background-color: var(--main-color);
            display: flex;
            flex-direction: column;
            justify-content: space-around;

            input{
                
                height: 40px;
                font-size: 16px;
                padding-left:  10px;
                border: none;
                border-radius: 5px;

            }
            button{
                width: 100%;
                height: 40px;
                border-radius: 5px;
                background-color: #d91921;
                color: white;
                border: none;                
                cursor: pointer;              
                font-size: 16px;
            
            }
            
            span{
                color: lightgray;
                
                b{
                    color: white;
                }
            }

        }

        

        
    }

    
}